.wrapper {
  max-width: 930px;
  margin: 0 auto;
}

.wrapper .nav {
  height: 50px;
  background: #a3a3a3;
  position: fixed;
}

.wrapper .nav nav {
  line-height: 50px;
}

.wrapper .nav nav a {
  margin: 5px;
  text-decoration: none;
  color: black;
  font-size: 20px;
  transition: color 0.7s;
}

.wrapper .nav nav .active {
  font-size: 23px;
  color: white;
}

.wrapper .section {
  padding-top: 50px;
}

.wrapper .section section div {
  margin: 10px;
}

.wrapper .section section div div {
  color: white;
  background: #a3a3a3;
  font-size: 20px;
}

.wrapper .button {
  background: url("../img/arrow.png");
  background-size: cover;
  width: 50px;
  height: 50px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  display: none;
  cursor: pointer;
}